@import '../../../styles/settings/colors';

.webdoor {
  &--item {
    height: 100%;
    position: absolute;
    width: 100%;
    will-change: transform;

    figure {
      box-sizing: border-box;
      display: none;
      margin: 0;
      height: 100%;
      pointer-events: none;
      position: relative;
      width: 100%;
      vertical-align: top;

      img {
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
        object-fit: cover;
        float: left;
        height: 100%;
        position: relative;
        width: 100%;
        vertical-align: top;
      }

      &:first-child {
        display: inline-block;
      }
    }

    &:after {
      background: rgb(2,0,36);
      background: linear-gradient(0deg, rgba($brown-900,0) 20%, rgba($brown-900,0) 30%, rgba($brown-900,0.9) 100%);
      box-sizing: border-box;
      content: '';
      display: inline-block;
      left: 0;
      height: 100%;
      opacity: 1;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }
}
